import styled from 'styled-components';
import Item from './Item';
import { useState } from 'react';
import AddSite from './AddSite';

const Wrapper = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  row-gap: 25px;
`;

interface ItemType {
  name: string;
  hidden: boolean;
  id: number;
}

const Items = () => {
  const [items, setItems] = useState<ItemType[]>([
    { name: 'Site 1', hidden: true, id: 1 },
  ]);

  const addItem = () => {
    setItems([...items, { hidden: true, id: items.length + 1, name: `Site ${items.length + 1}` }]);
  };

  const updateItems = (id: number) => {
    const newItems = items.map((item) => {
      if (item.id === id) {
        item.hidden = false;
      }
      return item;
    });
    setItems(newItems);
  };

  const removeSite = (id: number) => {
    console.log(id)
    setItems(items.filter((item) => item.id !== id));
  };

  return (
    <Wrapper>
      {items.map((item: any) => {
        return (
          <Item
            name={item.name}
            id={item.id}
            updateItems={updateItems}
            hidden={item.hidden}
            key={item.id}
            removeSite={removeSite}
          />
        );
      })}
      <AddSite addItem={addItem} />
    </Wrapper>
  );
};

export default Items;
